.body_overflow {
  overflow: hidden;
}
.model {
  position: fixed;
  top: 0;
  bottom: 0;
  right: -100%;
  z-index: 999;
  max-width: 1000px;
  background: #fff;
  margin: 0 auto;
  padding: 15px;
  transition: all 1s ease;
  box-sizing: border-box;
}
.model.show {
  right: 0;
}
.view-model {
  max-width: calc(100% - 200px);
  height: calc(100vh - 0px);
}
.model .model-content {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: calc(100% - 76px);
}
.modelBg {
  background: rgba(0, 0, 0, 0.2);
  z-index: 9;
  position: fixed;
  height: 100%;
  width: 100%;
  padding: 0;
  top: 0;
  left: 0;
}

footer.blockquote-footer.position-relative {
  overflow-x: auto;
  overflow-y: hidden;
}

@media screen and (max-width: 767px) {
  .view-model {
    max-width: calc(100% - 3px);
  }
}
