/*portfolio */
.portfolio {
  background-image: radial-gradient(rgba(0, 123, 255, 0.25), #ff01d4);
}
.portfolio-box {
  text-align: center;
  padding: 85px 0 60px;
  position: relative;
}

.portfolio-box .list-group-item.active {
  z-index: 0;
}

.portfolio-items .item {
  list-style: none;
  width: 20%;
  position: relative;
  overflow: hidden;
  padding: 5px;
}

.portfolio-items .projectDetail {
  position: absolute;
  height: 0%;
  width: 0%;
  top: 120%;
  left: 50%;
  background: rgba(14, 13, 14, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  transition: ease 1s all;
}

.portfolio-items .item:hover .projectDetail,
.projectDetail:hover {
  top: 10px;
  left: 10px;
  height: calc(100% - 20px);
  width: calc(100% - 20px);
  border: 1px dashed;
}

.category .list-group-item {
  background: transparent;
  color: #fff;
  border: 2px solid #fff;
  border-radius: 25px;
  margin-bottom: 10px;
  margin-left: 5px;
  cursor: pointer;
  font-family: arial;
  font-size: 14px;
  font-weight: bold;
}
.category .active {
  background: #fff;
  color: rgb(48, 44, 44);
}

@media screen and (max-width: 1200px) {
  .portfolio-items .item {
    width: 25%;
  }
}
@media screen and (max-width: 992px) {
  .portfolio-items .item {
    width: 33.33333%;
  }
}
@media screen and (max-width: 768px) {
  .portfolio-items .item {
    width: 50%;
  }
  .category {
    overflow-x: auto;
  }
}
